@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css");
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: #31304D;
  color: #222222;
}
.unScrolable{
  overflow-y: hidden;
}
.sectionOne{
  min-height: 10rem;
  background-color:rgb(22, 26, 48) ;
  display: flex;
  gap: 4rem;
  align-items: center;
  color: rgb(182, 187, 196);
}
.sectionTwo{
  margin-bottom: 3rem;
}
.hPt1{
  width: 8.5rem;
  height: 8.5rem;
  background-color: rgb(182, 187, 196);
  margin-left: 3rem ;
  border-radius: 80px;
}
.hPt1 .defaultPicture {
  cursor: pointer;
  width: inherit;
  height: inherit;
  border-radius: inherit;
  }
.defaultPicture img{
  width: inherit;
  height: inherit;
  border-radius: inherit;
}
.bigPicture{
  transition: 0.5s;
  height: inherit;
  width: inherit;
  border-radius: inherit;
  position: absolute;
  top: 1rem;
  z-index: -2;
}
.bigPicture span{
  display: none;
}
.bigPicture img{
  width: inherit;
  border-radius: inherit;
    height: inherit;
}
.hPt1 .openPicture {
  position: absolute;
  background-color: #000000c9;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  left: 0;
  top: 0.3rem;
  z-index: 1;
  width: 100%;
  height: 99%;
  border-radius: 0;
  /* transition: 0.2s; */
  overflow: hidden;
  }
.openPicture img {
  width: 35rem;
  height: 97%  ;
  margin-top: auto;
  margin-bottom: auto;
  border-radius: 0;
  cursor: pointer;
  transition: transform 0.3s;
  overflow: auto;
  }
.openPicture img.zoomed {
  transform: scale(2);
   /* cursor: move; */
  position: absolute;
}
.openPicture span{
  display: inline-block;
  position: absolute;
  right: 1rem;
  font-size: larger;
  font-weight: 600;
  color: white;
  text-shadow: black 3px 1px 1px;
  cursor: pointer;
}
.userName{
  grid-area: userName;
  color: rgb(152, 154, 158);}
.position{
  grid-area: position;
  color: rgb(152, 154, 158);
  font-size: 2px;
}
.degree{
  grid-area: degree;
  margin-left: 20px;
}
.address{
  grid-area: address;
  margin-left: 20px;
}
.linkidinLink{grid-area: linkidinLink;}
.email{grid-area: email;}
.phoneNumber{grid-area: phoneNumber;}
.cv{grid-area: cv;}

.hPt2{
  width: 65.5rem;
  height: 8.5rem;
  padding: 0.5rem;
  border-left: 1px solid rgb(182, 187, 196);
  font-size: 12px;
  display: grid;
  align-items: baseline;
  grid-template-areas: 'userName userName userName'
  'position position position'
  'degree phoneNumber linkidinLink'
  'address email cv';
  gap: 2px;
  margin-top: 7px;
  }
    .hPt2 .userName {
      display: inline-block;
      font-size: 35px;
      font-weight: bold;
      margin-top: -15px;
    }

  @media screen and (max-width: 948px){
    .hPt2 {
        width: 65.5rem;
        height: fit-content;
        padding: 0.5rem;
        border-left: 1px solid rgb(182, 187, 196);
        font-size: 12px;
        display: grid;
        align-items: baseline;
        grid-template-areas: 'userName userName userName'
          'position position position'
          'degree degree degree'
          'address address address'
          ' phoneNumber phoneNumber phoneNumber'
          'email email email'
          'linkidinLink linkidinLink linkidinLink'
          'cv cv cv';
        gap: 8px;
      }
    .degree{margin-left: 0;}
    .address{margin-left: 0;}
  }

  @media screen and (max-width:651px) {
    .sectionOne {
        min-height: 10rem;
        background-color: rgb(22, 26, 48);
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;
        align-items: center;
        justify-content: center;
        color: rgb(182, 187, 196);
      }
.hPt1{
  margin-left: auto;
  margin-right: auto;
  
}
