.bodySectionOne{
  height: 10rem;
  padding: 0.5rem;
  margin-bottom: 1.5rem;
  background-color: rgba(22, 26, 48, 0.171);
}
.bodySectionOne h1{
  font-size: 30px;
  color: white;
  text-shadow: rgb(22, 26, 48) 2px 1px 1px;
  padding-bottom: 5px;
}
.bodySectionOne p{
  max-height: 6rem;
  color: white;
  font-size: 15px;
  word-spacing: 0.8px;
  text-shadow: rgb(22, 26, 48) 2px 1px 1px;
  overflow: auto;
}
.bodySectionTwo{
display: flex;
gap: 6rem;
flex-wrap: wrap;
align-items: flex-start;
justify-content: center;
margin: auto 2rem;
}
.containers{
  width: 36rem;
  height: fit-content;
  border: #161A30 3px solid;
  border-radius: 7px;
  background-color: #b6bbc4c7;
}
@media screen and (max-width:640px){
  .containers {
      width: 26rem;
    }
}
@media screen and (max-width:482px) {
  .containers {
      width: 22rem;
    }
}
@media screen and (max-width:423px) {
  .containers {
    width: 19.5rem;
  }
.bodySectionTwo {
margin: auto 4px;
}
}
  .mediaSection{
  width: 100%;
  height: 15rem;
  border: #161A30 5px inset;
  border-right:#161A30 5px outset;
  border-bottom: none;
}

.mediaSection img {
  filter: blur(2px);
  width: 100%;
  height: 100%;
  transition: 0.25s;
}
.mediaSection .unblur{
  filter: none;
}
.textSectionOuter{
  border: #161A30 5px outset ;
  border-left: #161A30 5px inset ;
  border-top: none;
  height: 12rem;
  overflow-y: auto;
  margin-left: auto;
  margin-right: auto;
}
.textSectionInner{
width: 97%;
margin: 10px auto;

}
.textSectionOuter::-webkit-scrollbar {
  width: 10px;
}

.textSectionOuter::-webkit-scrollbar-thumb {
  background: #31304D;
  border-radius: 3px;
}
.projectData{
  display: block;
  margin-bottom: 5px;
  color: #F0ECE5;
  text-shadow: #1a1a1a 2.5px 1px 1px;
}
.projectName{
  text-align: center;
  font-size: 25px;
  font-weight: 700;
  color: #000;
  text-shadow:none;
  }
.projectDesc{
   margin-bottom: 0;
}
.pd1{
  margin-bottom: -4px;
}
.pd2 {
  margin-bottom: 0;
}
.projectDate{
  letter-spacing: 0.2px;
  color: #85898f;
  text-shadow: #1a1a1a  0.5px 1px 0.5px;
  font-size: 10px;
  margin-top: -4px;
}
.projectDate:hover{
  font-size: 11px;
  color: #000;
  text-shadow: none;
}
.miniTitle{
  font-weight: 700;
  font-size: large;
  color: #000;
  text-shadow: none;
  }
.containers span{
  cursor: default;
}
.dates{
  margin-bottom: 7px;
}
.ruleStyle{
  width: 80%;
  margin: -1px auto 6px;
  border-color: #000000b9;
}
.userData{
  display: flex;
  align-items: center;
  color: white;
}
.userData strong{
  margin-right: 3px;
  color: rgb(182, 187, 196);
}

.userData a img{
  height: 22px;
  width: 20px;
}
.userData a{
  text-decoration: none;
  color:rgb(0, 119, 181);
}
.projectRed{
  width: inherit;
  position: absolute;
  margin-top: 10px;
  padding-top: 4px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.projectRed a{
  display: flex;
  width: 1.2rem;
  align-items: center;
  justify-content: flex-start;
  margin-right: 1.5rem;
  padding-bottom: 3px;
  border: none;
  color: rgb(255, 255, 255);
  font-size: 30px;
  font-weight: 1000;
  background-color: rgb(22, 26, 48);
  height: 1.3rem;
  box-shadow: rgb(255, 255, 255) -2px 2px 16px;
}
.appsHeadline{
  color: white;
  text-align: center;
  margin-bottom: 1rem;
  font-size: 23px;
  text-decoration: underline 4px rgb(22, 26, 48);
  text-underline-offset: 4px;
}
.separateRule{
  width: 80%;
  border: 1px solid rgb(22, 26, 48);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0.5rem;
}